  methods: {
    click(index) {
      this.itemIndex = index;
      this.$refs.swiper.swiper.slideTo(index);
    },
    back() {
      this.$router.go(-1);
    }
  },
  mounted() {
    this.click(this.$route.query.id - 1);
    this.status = this.$route.query.status;
  }
};
